          a.share-icon.twitter(href="http://twitter.com/share?url=https://theinitium.com/project/20220331-mainland-digital-sexual-violence/c.html%23t&text=曝光很快引來了報復，發聲的女生也都被 「獻祭 」到色情群組中。", target="_blank")
        p.title 被人肉的發聲者
        p.author 端傳媒實習記者 張晉谷
        div.article
          p 2021年12月，梁州在她的微信公眾號發出一篇文章，指出一名女生被人將生活照發佈到Telegram「盯射群」供人意淫，而發佈者是這名女生相識六年的男性好友。文章發出後，將近30個女生先後聯繫梁州，表示自己也是受害者。她們的生活照、私密照也被散播在Telegram、Twitter等平台，大部分女生指是熟人作案。
          p 不久，中國視頻網站Bilibili的一名博主雨悅也製作了視頻，講述她在Telegram「盯射群」臥底48小時的經歷，截至目前，這期視頻播放量已超過250萬。雨悅發現群中用戶們「無時無刻地『獻祭』自己的女友、前女友、朋友、網友，甚至是陌生人乃至母親，給陌生猥瑣男意淫，他們把那些無辜的女生稱作『母狗』、『母豬』。」
          p 對於這類基於Telegram、Twitter以及大量色情網站的未經同意的私密影像散播，近年媒體已有報道。根據<a href="https://www.bbc.com/news/world-60303769" target="_blank">BBC</a>、<a href="https://www.scmp.com/week-asia/long-reads/article/3135803/abuse-and-anger-inside-online-groups-spreading-stolen-sexual" target="_blank">SCMP</a>等報道，受害的包括在馬來西亞、俄羅斯、巴西、肯尼亞等至少20個國家的女生，一些群組更散播未成年女生的影像。而梁州、雨悅以及後續一系列中國媒體的報道，則讓存在於防火牆外的中文色情群組第一次曝光在中國民眾的視野。
          p 然而，曝光很快引來了報復，發聲的女生也都被「獻祭」到色情群組中。
          div.img
            img(src="./../../assets/imgs/news-4-1.png")
            p Bilibili博主雨悅（化名）製作臥底視頻後，被色情群組中人辱罵、換臉修圖。
          h3 被洩漏的海量中國公民信息
          p 中國網民的信息洩露情況非常嚴重。2020年3月，警方發現有暗網用戶售賣「5.38億微博用戶綁定手機號數據」。同年10月，江蘇蘇州警方曾破獲一宗通過Telegram出售公民身份信息的案件，「查獲非法獲取的各類公民個人信息數百萬條」。
          p 在以簡體中文交流的Telegram群組中，端傳媒記者發現有人出售監控攝像頭權限，部分酒店、家庭中的監控攝像頭被破解，購買者可以借此實時觀看他人家裡的監控。群組中還有人發佈女性在家中換衣服等家庭監控拍下的視頻。
          p 另外，有人在Telegram上開設了一個免費的人肉搜查工具。這個工具名為「社工庫機器人」，利用Telegram所提供的頻道和機器人功能，集合多個洩露的中國公民信息數據庫，使用者只需要輸入關鍵詞，便可查詢到身份證號、電話號碼、微博賬號、甚至快遞地址等隱私信息。雨悅推測，自己便是被「盯射群」成員找到微博賬號，再用微博賬號在「社工庫機器人」中查詢出了身份信息。
          p 雨悅說，因為厭惡群中內容，臥底48小時後她便退出群組。後來，有網友私信告知她已經被人肉，甚至進行改圖換臉，將她的臉移植到其他色情照上，並製作成影片。她真實的身份信息也被曝露——有人在群組中曝光了她的真實姓名、身份證號和家庭住址，精確到她家的門牌號碼。
          p 因為人在外國留學，她委託國內朋友幫忙報警。警察沒有立案，也沒有表示不立案的原因，同時建議雨悅找所在國的中國大使館處理。
          p 雨悅曾經就此咨詢律師。律師表示，唯一解決辦法是找國外的律師起訴Telegram，要求Telegram查找發佈用戶的信息，拿到用戶信息再報警才有用，「但咱們都知道這是不可能的。」
          p Telegram的隱私政策中規定，只有收到法庭文件，證實用戶是恐怖份子嫌疑人時，才可能向政府提交用戶的IP地址及電話號碼。從創辦至今，Telegram還從未向任何政府提交過用戶信息。
          p 在回應BBC的一份聲明中，Telegram表示其會主動監視公共空間，並且處理用戶對違反其服務條款內容的舉報。但Telegram沒有確認該平台是否允許未經同意發佈人們的私密圖片，也沒有確認這些圖片是否被刪除。
          p 「沒有嫌疑人範圍這件事情幾乎無解，」雨悅告訴端傳媒，「但是如果有機會或者能鎖定人肉我的人，我一定會維權的。」
          p 發佈微信文章的第二天，梁州發現文章的閱讀量快速上漲，她開始擔憂，自己可能會被人肉。
          p 她於是請專門從事社交媒體研究的朋友幫忙檢查了她的社交賬號，刪除了社交平台所有公開的露臉照片，並刪掉了100多名平常沒有聯繫的微信好友，還多次在Telegram頻道「社工庫機器人」中嘗試搜索自己，確認自己的身份信息沒有被洩漏。
          p 一個月後，還是有賬號在「盯射群」中發佈梁州微信朋友圈裡的自拍照片，發佈者同時辱罵她「臭騷X，長得那麼騷」，另外，她微信公眾號所綁定的真實姓名也被發佈在群中。
          p 梁州又排查了一遍微信好友，「把覺得奇怪的人又都刪掉了」，然後更換了手機號碼，並重新綁定微信、支付寶和銀行卡等等。梁州隨後向當地派出所報警，一名輔警給梁州錄了兩個多小時的口供，在中國大陸，錄口供屬執法行為，正式警察才有權進行。
          p 梁州表示，輔警說不知道什麼是Telegram，並轉告梁州，「需要外交部去聯繫外網」，否則沒有辦法。
          div.img
            img(src="./../../assets/imgs/news-4-2.jpg")
            p 央視有關「掃黃打非」的新聞報導。
          h3 把她們的遭遇告訴更多人
          p 梁州的文章也引起了不少中國媒體的跟蹤報道。《新京報》、《澎湃新聞》、《南方週末》等各大媒體都發出長篇報道，主要講述受害者的經歷和感受。發稿後，《新京報》記者告知受害者，「掃黃打非辦」聯繫上記者，要求提供資料。
          p 「掃黃打非辦」即全國掃黃打掃工作小組辦公室，是中宣部下屬部門，與中宣部反非法反違禁局實質上是同一個機構，近年多次發起針對網絡上敏感政治內容、色情內容的「淨網」、「護苗」行動。
          p 兩名受害者向端傳媒表示，自己通過《新京報》記者向「掃黃打非辦」提交了蒐集到的資料和聯繫方式，但至今未有反饋。
          p 「我們聯繫了網警，聯繫了媒體，也沒有比較好的解決辦法，現在的感覺是順其自然了。從頭到尾就是接受了採訪，發了一些稿子而已，還是只有我一個人在處理。」其中一名當事人張振對端傳媒表示。被洩漏和散播的，是他和女友親密時拍下的照片。
          p 這些色情群組中，總是有新的受害者出現。張振最近看到，又有十多個Twitter賬號在轉發同一個女生的私密視頻。
          p 「對於我們這樣的受害者來講，可能有新的人會把我們慢慢的蓋下去，但是這個問題解決不了，就永遠會有新的受害者。這完全就是一個死循環。」張振說，他決定之後去報警。
          p 「我覺得解決的辦法最終都要落在執法上。社會再怎麼同情，社會也幫助不了我們，群眾也幫助不了我們，人民也幫助不了我們，能幫我們的只有公安機關。」張振說，即便他知道，目前已知的受害者中，比較有效的報警結果是讓發佈者治安拘留5天。
          p 在香港，此前也並沒有直接針對發佈私密影像的罪行。2021年10月8日，香港修訂《刑事罪行條例》，新增了「窺淫」、「未經同意下發佈私密影像」等四項罪名，最高刑罰均為監禁5年。
          p 研究性別議題多年的香港嶺南大學社會學及社會政策系副教授陳效能告訴端傳媒，推動設立「窺淫罪」等罪名前後花費了多年。2018年，香港關注婦女性暴力協會便遞交意見書，建議「新增之窺淫罪應涵蓋偷拍裙底」。但即便有了相應罪名，依然很難對抗這類Telegram群組。
          p 2020年5月，一名香港註冊專科醫生因在香港類似的Telegram群組中多次發佈一女子的私密部位影像，被控四項發佈淫穢物品罪，<a href="https://news.mingpao.com/pns/%E6%B8%AF%E8%81%9E/article/20220222/s00002/1645467269155/%E9%86%AB%E7%94%9F%E8%A2%AB%E6%8E%A7tg%E8%B2%BC%E5%A5%B3%E5%AD%90%E6%B7%AB%E7%85%A7-%E4%BA%8B%E4%B8%BB%E4%B8%88%E5%A4%AB%E4%BD%9C%E4%BE%9B%E5%93%BD%E5%92%BD" target="_blank">近日案件正在審訊</a>。事主經人告知進入群組後，發現發佈者電話號碼與這名被告相同，才確認身份。不過，端傳媒記者在相關群組中所見，絕大部分發佈者都隱藏了電話號碼。
