          p 她同時指出，社會也需要對青少年進行合適的性教育。「讓他們意識到經常這樣消費女性其實是扭曲了他們怎麼看女性，也會miss out（錯失）其他視角去看這個世界，」陳效能說，「要讓他們意識到這樣的行為不僅是對他人的傷害，對自己也是沒有好處的。」
          p 梁州還在念大學，最初找到她的那名受害者，是她初中寫小說時結識的老讀者。這件事後，梁州向一家媒體投遞了實習生簡歷。她此前一直為各家新媒體供稿，寫營銷稿件。
          p 「在各個大號遊蕩的過程裏，我曾經只為了錢而寫作過，那段時間我獲得了豐厚的收益，但是過得很痛苦，可是N號房這件事，與其說我在幫助其他人，我覺得它其實也在成就我，尤其是在和媒體對接，幫受害人發聲的時候，我會覺得自己被需要，我自己是有力量的，這對我很重要。」梁州說。
          p 「我知道，我需要寫下去，需要把她們遭受的一切告訴更多人，這件事一定有意義。」
          p （文中梁州、雨悅、張振為化名。）
