<template lang="pug">
div.Telegram-page(ref="TelegramPage")
  Telegram-chat-item.animate__animated.animate__fadeInUp(
    v-for="(item, index) in messages",
    :key="index",
    :name="item.name",
    :name-color="item.nameColor",
    :time="item.time",
    :reply="item.reply",
    :type="item.type",
    :text-content="item.text",
    :srcs="item.srcs"
  )
</template>

<script>
import TelegramChatItem from '@/components/TelegramChatItem'
import sleep from '@/utils/sleep'
import { clearAllBodyScrollLocks } from 'body-scroll-lock'

const PerfectScrollbar = window.PerfectScrollbar

const messages = [
  {
    name: 'LF',
    nameColor: '#​​99CF00',
    type: 'text',
    text: '有點性慾思想都觸犯到女拳了？',
    time: '7:22 PM'
  },
  {
    name: '小丑 又',
    nameColor: '#6E6BFA',
    type: 'text',
    text: '你可以在網絡公開發照片，別人就不能意淫？',
    time: '7:38 PM'
  },
  {
    name: 'cc',
    nameColor: '#B186DD',
    type: 'text',
    text: '這是那篇文章的作者，真實姓名xxx，身份證號xxxxx，地址xxxxx',
    time: '8:02 PM'
  }
]

export default {
  name: 'TelegramPage',
  components: {
    TelegramChatItem
  },
  data () {
    return {
      isActive: false,
      messages: [],
      index: 0
    }
  },
  methods: {
    async push () {
      this.messages.push(messages[this.index])
      await sleep(1000)
      if (this.isActive) {
        this.$refs.TelegramPage.scrollTop = this.$refs.TelegramPage.scrollHeight
      }
      if (this.index < messages.length - 1) {
        this.index++
        this.push()
      } else {
        clearAllBodyScrollLocks()
      }
    }
  },
  mounted () {
    this.isActive = true
    this.push()

    const ps = new PerfectScrollbar(this.$refs.TelegramPage, {
      suppressScrollX: true,
      useBothWheelAxes: true
    })
    ps.update()
  },
  beforeDestroy () {
    this.isActive = false
    this.messages = []

    clearAllBodyScrollLocks()
  }
}
</script>

<style scoped lang="scss">
.Telegram-page {
  height: 100%;
  overflow: hidden;
  color: #FFF;
}
</style>
